import moment from 'moment'

export default {
  methods: {
    goToPath (path) {
      this.$router.push(path)
    },
    formatDate (value, formattedAs='MMM D, YY') {
      let oldDate = value.toString()
      oldDate = oldDate.replace('{ts ', '').replace('}', '')
      return moment(oldDate).format(formattedAs)
    },
    isNumeric(n) {
      return !isNaN(parseFloat(n)) && isFinite(n);
    },
    compareElements (a,b) {
      const aSortBy = a[this.sortBy].toUpperCase()
      const bSortBy = b[this.sortBy].toUpperCase()
      let comparison = 0
      if (aSortBy > bSortBy) { comparison = 1 }
      else if (aSortBy < bSortBy) { comparison = -1 }
      return comparison
    },
    mailTo: function ( emailAddress ) {
      window.open('mailto:'+ emailAddress)
    }
  }  
}
